import React,{Component} from 'react';
import React_DOM,{render} from 'react-dom'
import {connect} from 'react-redux'
import * as action from '../../redux/actions'
import Header from '../../components/header'
import {getOneBook,updateBook} from '../../api'
import './index.css'
class Update extends Component{
    constructor(){
        super();
    }
    componentDidMount(){
        let id=this.props.match.params.id;
        getOneBook(id).then(res=>{
            this.name.value=res.data.bookName;
            this.bookCover.value=res.data.bookCover;
            this.content.value=res.data.content;
        })
    }
    edit=()=>{
        let id=this.props.match.params.id;
        let book={
            bookName:this.name.value,
            bookCover:this.bookCover.value,
            content:this.content.value,
        };
        updateBook(id,book).then(res=>{
            this.props.history.push('/list');
        })
    };
    render(){
        return(
            <div>
                <Header title="">
                </Header>
                <div className="add">
                    <div className="group">
                        <label htmlFor="bookName"></label>
                        <input type="text" id="bookName" placeholder="" ref={ref=>this.name=ref}/>
                    </div>
                    <div className="group">
                        <label htmlFor="bookCover"></label>
                        <input type="text" id="bookCover" placeholder="" ref={ref=>this.bookCover=ref}/>
                    </div>
                    <div className="group">
                        <label htmlFor="content"></label>
                        <input type="text" id="content" placeholder="" ref={ref=>this.content=ref}/>
                    </div>
                </div>
                <div className="choose">
                    <button onClick={this.edit}></button>
                    <button onClick={()=>{this.props.history.push('/list')}}></button>
                </div>
            </div>
        )
    }
}
export default connect((state)=>({
    book:state.book
}),action)(Update)


